@-webkit-keyframes rotate {

  100% {
    right: -70px;
    top: 26px;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    transition: 3s ease;
  }

}

@keyframes rotate {

  100% {
    right: -70px;
    top: 26px;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    transition: 3s ease;
  }

}

@-webkit-keyframes roll {

  100% {
    right: 5px;
    top: -20px;
  }

}

@keyframes roll {

  100% {
    right: 5px;
    top: -20px;
  }

}

@-webkit-keyframes fade {

  100% {
    opacity: 1;
    transition: 3s ease;
  }

}

@keyframes fade {

  100% {
    opacity: 1;
    transition: 3s ease;
  }

}

@-webkit-keyframes balance {

  0% {
    opacity: 1;
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
    transition: 3s ease;
  }

  50% {
    opacity: 1;
    -webkit-transform: rotate(3deg);
            transform: rotate(3deg);
    transition: 3s ease;
  }

  100% {
    opacity: 1;
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
    transition: 3s ease;
  }

}

@keyframes balance {

  0% {
    opacity: 1;
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
    transition: 3s ease;
  }

  50% {
    opacity: 1;
